// ====================  Default  =========================


/*! Defaults */
body {
  color: $main-color;
  font-family: Helvetica3;
  @include font-size(16px); }

// sticky footer (always at the bottom of page)

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%; }

.content {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

